.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 1000ms var(--cubick-link);
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: rotateY(20deg)rotateX(35deg)translate(-50px, -50px)skew(-35deg, 10deg);
}

.modal {
  width: 100vw;
  height: 100vh;
  transform: rotateY(0) rotateX(0deg) translate(0%, 0%) skew(0deg, 0deg);
  opacity: 1;
  transition: transform 1000ms var(--cubick-link);
  border: 2px solid #111;
  border-radius: 18px;
  padding: 40px;
  position: absolute;
  top: 0%;
  left: 0%;
}

@media screen and (min-width: 768px) {
  .modal {
    transform: rotateY(0) rotateX(0deg) translate(-50%, -50%) skew(0deg, 0deg);
